(function () {
  'use strict';

  angular.module('BlurAdmin.pages.productos')
      .controller('productosItemController', productosItemController);

  productosItemController.$inject = ['$log', '$stateParams', '$rootScope', 'serverAPI', 'Restangular'];
  function productosItemController($log, $stateParams, $rootScope, serverAPI, Restangular) {
    $log.log('productosItemController');
    var vm = this;
    vm.producto = {};
    vm.ferreterias = [];
    vm.cantidades = [];

    vm.remove = function () {
      var deleteMessage = $uibModal.open({
        animation: true,
        templateUrl: 'app/pages/productos/borrar/borrar.template.html',
        controller: 'productosBorrarController',
        controllerAs: 'vm',
        resolve: {}
      });

      deleteMessage.result.then(function() {
        Restangular.one('productos', vm.producto.id).remove().then(function() {
          $state.go('productos', {id: vm.producto.almacen.id});
        });
      }, function() {
        $log.log('Borrar fue cancelado')
      });
    };
    
    serverAPI.productos.get($stateParams.idProducto).then(function (resp) {
      if (resp.pImagen !== '') {
        resp.pImagen = resp.pImagen.indexOf('http://') >= 0 || resp.pImagen.indexOf('https://') >= 0 ?
          resp.pImagen : $rootScope.baseURL + 'api/fileentry/get/' + resp.pImagen;
      }

      vm.producto = {
        id: resp.id,
        pNombre: resp.pNombre || '',
        pDescripcion: resp.pDescripcion || '',
        pImagen: resp.pImagen || '/assets/pictures/empty.png',
        pPrecio: resp.pPrecio || 0,
        pCantidadEnMano: resp.pCantidadEnMano || 0
      };
    });
  }
})();

(function () {
  'use strict';

  angular.module('BlurAdmin.pages.productos')
      .controller('productosListaController', productosListaController);

  productosListaController.$inject = ['$log', '$state', '$stateParams', '$uibModal', '$rootScope', 'serverAPI', 'Restangular', 'localStorageService'];
  function productosListaController($log, $state, $stateParams, $uibModal, $rootScope, serverAPI, Restangular, localStorageService) {
    $log.log('productosListaController');
    var vm = this;
    vm.user = localStorageService.get('user') || {};
    vm.productos = [];
    vm.busqueda = {nombre: ''};
    vm.isLoading = false;
    console.log(vm.user)
    vm.goTo = function(id) {
      $state.go('productos_item', {id: $stateParams.id, idProducto: id});
    };

    vm.addProduct = function () {
      if (vm.user.privilegio === 'vendedor') {
        $state.go('productos_registrar', {id: vm.user.vendedor.idAlmacen});
      } else {
        $state.go('productos_registrar', {id: $stateParams.id});
      }
    };

    vm.remove = function ($index, id) {
      var deleteMessage = $uibModal.open({
        animation: true,
        templateUrl: 'app/pages/productos/borrar/borrar.template.html',
        controller: 'productosBorrarController',
        controllerAs: 'vm',
        resolve: {}
      });

      deleteMessage.result.then(function() {
        Restangular.one('productos', id).remove().then(function() {
          vm.productos.splice($index, 1);
        });
      }, function() {
        $log.log('Borrar fue cancelado')
      });
    };

    vm.buscar = function () {
      var id = vm.user.privilegio === 'vendedor' ? vm.user.vendedor.idAlmacen : $stateParams.id;
      Restangular.one('almacenes', id).customGET('productos', vm.busqueda).then(function (resp) {
        vm.productos = ((resp || {}).productos || []).map(function(item) {
          item.pImagen = item.pImagen.indexOf('http://') >= 0 || item.pImagen.indexOf('https://') >= 0 ?
            item.pImagen : $rootScope.baseURL + 'api/fileentry/get/' + item.pImagen;
          return item;
        });

        vm.isLoading = true;
      });
    };
    
    vm.buscar();
  }
})();

(function () {
  'use strict';

  angular.module('BlurAdmin.pages.productos')
      .controller('productosRegistroController', productosRegistroController);

  productosRegistroController.$inject = ['$log', '$state', '$stateParams', '$rootScope', 'serverAPI', 'Restangular', 'localStorageService', 'Upload'];
  function productosRegistroController($log, $state, $stateParams, $rootScope, serverAPI, Restangular, localStorageService, Upload) {
    $log.log('productosRegistroController');
    var vm = this;
    vm.user = localStorageService.get('user') || {};

    vm.proveedores = [];
    vm.producto = {
      idProveedor: 0,
      idAlmacen: vm.user.privilegio === 'vendedor' ? vm.user.vendedor.idAlmacen : +$stateParams.id,
      pNombre: '',
      pDescripcion: '',
      pImagen: '/assets/pictures/empty.png',
      pPrecio: 0,
      pCantidadEnMano: 0
    };

    vm.submit = function() {
      serverAPI.productos.post(vm.producto).then(function(resp) {
        if (vm.user.privilegio === 'vendedor') {
          $state.go('productos_almacen');
        } else {
          $state.go('productos', {id: $stateParams.id});
        }
      });
    };

    vm.upload = function (files) {
      if (!vm.file.$error) {
        Upload.upload({
          url: $rootScope.baseURL + 'api/fileentry/add',
          data: {file: vm.file}
        }).then(function (resp) {
          vm.producto.pImagen = ((resp || {}).data || {}).filename || '';
          vm.submit();
        });
      }
    };

    Restangular.all('proveedores').getList().then(function(resp) {
      vm.proveedores = resp || [];
    });
  }
})();

